import React from 'react';
import { useTranslation } from 'react-i18next';


function Schedule() {
  const { t } = useTranslation();

  //    PDF-
  const pdfUrl = '/Schedule 2025-2026.pdf';


  return (
    <div className="page schedule-page">
      <div className="home-content">
        <h1>{t('schedule.title')}</h1>
         {/*  PDF-   <iframe>.
           'src'    .
        */}
        <iframe
          title="School Charter"
          src={pdfUrl}
          width="100%"
          height="800px" // ,     
          style={{ border: 'none' }}
        >
          <p>     PDF-. <a href={pdfUrl}> ,   .</a></p>
        </iframe>

        <p style={{ textAlign: 'center' }}>{t('calendar.text')}</p>
      
      {/*      Google Classroom */}
        <a 
        href="https://classroom.google.com/c/NzE0MDYxMjUyNTkz" 
        target="_blank" 
        rel="noopener noreferrer" 
        className="google-classroom-button"
      >
         <img src="/images/google-classroom.png" alt="Google Classroom Icon" className="button-icon" />
        {t('Google Classroom')}
      </a>
      </div>
    </div>
  );
}

export default Schedule;